
.header {
    height: 50px;
    border-bottom: 1px solid #efefef;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid rgba(0,0,0,.0975);
}